import Vue from 'vue'
import Router from 'vue-router'
import skill from '../components/skill.vue'
import about from '../components/about.vue'

Vue.use(Router)

export default new Router({
    routes:[
        {
            path:'/',
            name:'skill',
            component:skill
        },
        {
            path:'/about',
            name:'about',
            component:about
        }
    ]
})



